@use "base/variables" as v;
@use "base/mixins" as m;

main {
  position: relative;
}

.main-services {
  position: absolute;
  top: 80rem;
}

.main-description {
  position: absolute;
  top: 175rem;
}

.grid-container {
  position: absolute;
  top: 250rem;
}

.main-hero {
  background-image: url("../img/home/background.png");
  background-repeat: no-repeat;
  background-size: contain;
  height: 140rem;
  .hero-text {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: v.$title;
    color: v.$primaryBlueDark;
    font-size: 2rem;
    margin: auto;
    h2 {
      margin-top: 30rem;
      text-align: center;
    }
  }
  .img-container {
    margin-top: 5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 15rem;
    img {
      height: 100%;
    }
  }
}
