html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  line-height: 1.5;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.header-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 5rem;
  background-color: #dfe7ec;
  font-family: "CamingoDosProCd-SemiBold";
}
.header-top p {
  font-size: 1.5rem;
  color: #747474;
}

.header-phone {
  display: flex;
  align-items: center;
}
.header-phone > * {
  margin: 1rem;
}
.header-phone .img-container {
  height: 2rem;
  padding-left: 3rem;
}
.header-phone .img-container img {
  height: 100%;
}

.header-social-media {
  display: flex;
  align-items: center;
}
.header-social-media .img-container {
  height: 2rem;
  margin: 3rem;
}
.header-social-media .img-container:last-child {
  padding-right: 2rem;
}
.header-social-media .img-container img {
  height: 100%;
}

.header-middle {
  height: 20rem;
  background-color: #1A9BC4;
  display: flex;
  align-items: center;
  justify-content: center;
}
.header-middle .img-container {
  height: 12.5rem;
}
.header-middle .img-container img {
  height: 100%;
}

.header-bottom {
  height: 10rem;
  background-color: #dfe7ec;
  display: flex;
  align-items: center;
}
.header-bottom .navbar {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.header-bottom .navbar .navbar-element {
  display: flex;
  align-items: center;
}
.header-bottom .navbar .navbar-element .img-container {
  height: 3rem;
}
.header-bottom .navbar .navbar-element .img-container img {
  height: 100%;
}
.header-bottom .navbar .navbar-element p {
  font-family: "CodeProBoldLC";
  font-size: 1.75rem;
  margin: 2rem;
}

main {
  position: relative;
}

.main-services {
  position: absolute;
  top: 80rem;
}

.main-description {
  position: absolute;
  top: 175rem;
}

.grid-container {
  position: absolute;
  top: 250rem;
}

.main-hero {
  background-image: url("../img/home/background.png");
  background-repeat: no-repeat;
  background-size: contain;
  height: 140rem;
}
.main-hero .hero-text {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Gotham-Bold";
  color: #1A9BC4;
  font-size: 2rem;
  margin: auto;
}
.main-hero .hero-text h2 {
  margin-top: 30rem;
  text-align: center;
}
.main-hero .img-container {
  margin-top: 5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 15rem;
}
.main-hero .img-container img {
  height: 100%;
}

.main-services {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 8rem;
  margin: 15rem;
}
.main-services .service {
  width: 100%;
  height: 75rem;
  border-radius: 30rem 30rem 0 0;
  background-color: #dfe7ec;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  position: relative;
}
.main-services .service .img-container {
  height: 7rem;
  margin-top: 10rem;
  margin-bottom: 5rem;
}
.main-services .service .img-container img {
  height: 100%;
}
.main-services .service .service-text {
  font-family: "BaseticaPro-Med";
  margin: 8rem 6rem;
  text-align: center;
  font-size: 2.5rem;
  color: #747474;
}
.main-services .service .service-btn {
  position: absolute;
  bottom: -2.25rem;
  font-family: "Gotham-Bold";
  font-size: 2.5rem;
  background-color: #2ba7dd;
  color: #ffffff;
  text-align: center;
  border-radius: 3rem;
  padding: 1rem 8rem;
}
.main-services .service .service-btn-odd {
  background-color: #ffccd9;
  color: #e26d93;
}

.main-description {
  margin: 10rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 5rem;
}
.main-description .description-image {
  width: 100%;
}
.main-description .description-image .img-container {
  width: 100%;
}
.main-description .description-image .img-container img {
  width: 100%;
}
.main-description .description-text h3 {
  color: #1A9BC4;
  font-family: "Gotham-Bold";
  font-size: 3rem;
  text-align: center;
}
.main-description .description-text p {
  font-family: "BaseticaPro-Med";
  color: #747474;
  font-size: 1.5rem;
  text-align: center;
}

.grid-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.main-grid {
  width: 75%;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(5, 1fr);
  gap: 2rem;
}
.main-grid > * {
  color: #ffffff;
  font-family: "BaseticaPro-Med";
  font-size: 2rem;
}
.main-grid .img-container {
  height: 10rem;
}
.main-grid .img-container img {
  height: 100%;
}

.grid-1 {
  grid-area: 1/1/2/5;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #1A9BC4;
  border-radius: 4rem 0 0 0;
}

.grid-2 {
  grid-area: 2/1/4/5;
  background-color: #1A9BC4;
}

.grid-3 {
  grid-area: 1/5/4/9;
  background-color: #2BAFE5;
}

.grid-4 {
  grid-area: 1/9/4/13;
  background-color: #1A9BC4;
}

.grid-5 {
  grid-area: 4/1/6/4;
  background-color: #2BAFE5;
}

.grid-6 {
  grid-area: 4/4/6/10;
  background-color: #1A9BC4;
}

.grid-7 {
  grid-area: 4/10/6/13;
  background-color: #2BAFE5;
  border-radius: 0 0 4rem 0;
}

.grid-1,
.grid-2,
.grid-3,
.grid-4,
.grid-5,
.grid-6,
.grid-7 {
  display: flex;
  padding: 2rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}