@use "base/variables" as v;
@use "base/mixins" as m;

.main-description {
    margin: 10rem;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 5rem;
    .description-image {
        width: 100%;
        .img-container {
            width: 100%;
            img {
                width: 100%;
            }
        }
    }
    .description-text {
        h3 {
            color: v.$primaryBlueDark;
            font-family: v.$title;
            font-size: 3rem;
            text-align: center;
        }
        p {
            font-family: v.$text;
            color: v.$secondaryGray;
            font-size: 1.5rem;
            text-align: center;
        }
    }
}