@use "base/variables" as v;
@use "base/mixins" as m;

.grid-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.main-grid {
    width: 75%;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(5, 1fr);
  gap: 2rem;
  & > * {
    color: v.$white;
    font-family: v.$text;
    font-size: 2rem;
  }
  .img-container {
    height: 10rem;
    img {
        height: 100%;
    }
  }
}

.grid-1 {
  grid-area: 1 / 1 / 2 / 5;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: v.$primaryBlueDark;
  border-radius: 4rem 0 0 0;
}
.grid-2 {
  grid-area: 2 / 1 / 4 / 5;
  background-color: v.$primaryBlueDark;
}
.grid-3 {
  grid-area: 1 / 5 / 4 / 9;
  background-color: v.$primaryBlue;
}
.grid-4 {
  grid-area: 1 / 9 / 4 / 13;
  background-color: v.$primaryBlueDark;
}
.grid-5 {
  grid-area: 4 / 1 / 6 / 4;
  background-color: v.$primaryBlue;
}
.grid-6 {
  grid-area: 4 / 4 / 6 / 10;
  background-color: v.$primaryBlueDark;
}
.grid-7 {
  grid-area: 4 / 10 / 6 / 13;
  background-color: v.$primaryBlue;
  border-radius: 0 0 4rem 0;
}

.grid-1,
.grid-2,
.grid-3,
.grid-4,
.grid-5,
.grid-6,
.grid-7 {
    display: flex;
    padding: 2rem;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
