@use 'base/variables' as v;
@use 'base/mixins' as m;

.header-top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 5rem;
    background-color: v.$grayBackground;
    font-family: v.$textNarrow;
    p {
        font-size: 1.5rem;
        color: v.$secondaryGray;
    }
}

.header-phone {
    display: flex;
    align-items: center;
    &>* {
        margin: 1rem;
    }
    .img-container {
        height: 2rem;
        padding-left: 3rem;
        img {
            height: 100%;
        }
    }
}

.header-social-media {
    display: flex;
    align-items: center;
    .img-container {
        height: 2rem;
        margin: 3rem;
        &:last-child {
            padding-right: 2rem;
        }
        img {
            height: 100%;
        }
    }
}

.header-middle {
    height: 20rem;
    background-color: v.$primaryBlueDark;
    display: flex;
    align-items: center;
    justify-content: center;
    .img-container {
        height: 12.5rem;
        img {
            height: 100%;
        }
    }
}

.header-bottom {
    height: 10rem;
    background-color: v.$grayBackground;
    display: flex;
    align-items: center;
    .navbar {
        width: 100%;
        display: flex;
        justify-content: space-around;
        align-items: center;
        .navbar-element {
            display: flex;
            align-items: center;
            .img-container {
                height: 3rem;
                img {
                    height: 100%;
                }
            }
            p {
                font-family: v.$titleBold;
                font-size: 1.75rem;
                margin: 2rem;
            }
        }
    }
}