@use "base/variables" as v;
@use "base/mixins" as m;

.main-services {
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 8rem;
    margin: 15rem;
    .service {
        width: 100%;
        height: 75rem;
        border-radius: 30rem 30rem 0 0;
        background-color: v.$grayBackground;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: auto;
        position: relative;
        .img-container {
            height: 7rem;
            margin-top: 10rem;
            margin-bottom: 5rem;
            img {
                height: 100%;
            }
        }
        .service-text{
            font-family: v.$text;
            margin: 8rem 6rem;
            text-align: center;
            font-size: 2.5rem;
            color: v.$secondaryGray;
        }
        .service-btn {
            position: absolute;
            bottom: -2.25rem;
            font-family: v.$title;
            font-size: 2.5rem;
            background-color: v.$blueBtn;
            color: v.$white;
            text-align: center;
            border-radius: 3rem;
            padding: 1rem 8rem;
        }
        .service-btn-odd {
            background-color: v.$pinkBtn;
            color: v.$pinkText;
        }
    }
}